.vz-product-thumb {
    overflow: hidden;
    z-index: 1;
    position: relative;
    width: 100%;
    display: block;
    min-height: 400px;
    height: 100%;
    &:hover {
        overflow: visible;
        z-index: 2;
        .vz-product-thumb--wrap {
            position: absolute;
            width: 100%;
            background-color: #fff;
            box-shadow: 0px 3px 43px 0px rgba(0, 0, 0, 0.16);
        }

        .vz-product-thumb__options  ~ *:not(script){
            opacity: 1;
            display: block;
        }
    }
    .vz-product-thumb__options ~ *:not(script) {
        display: none;
    }
    .vz-rating {
        margin-bottom: 20px;
    }

    .btn-buy {
        text-transform: uppercase;
        font-weight: bold;
    }

    &--wrap {
        padding: 15px;
    }



    &__price-block {
        font-size: 16px;
        font-weight: bold;
        margin-bottom: 10px;
    }

    &__badge {
        position: absolute;
        left: 0;
        background-color: #bd6356;
        color: #fff;
        padding:5px 10px;
    }
    &__brand {
        font-weight: bold;
    }

    &__title {
        font-size: 16px;
    }

    &__image {
        position: relative;
        margin-bottom: 15px;
        img {
            margin: auto;
        }
    }

    .jag-heart {
        position: absolute;
        right: 5px;
        top: 5px;
    }

    .product-list & {
        &--wrap{
            display: flex;
        }
        &__title,&__price-block{
            font-size: 25px;
            margin-bottom: 15px;
            display: block;
        }
        &__image{
            flex: 0 0 30%;
            margin-right: 20px;
        }
        .btn-buy{
            width: auto;
            display: block;
        }
    }
}
